@import 'src/style/variable.scss';

.pagination {
  width: 100%;
  height: 130px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  li {
    width: 35px;
    height: 35px;
    transition: 0.2s ease 0s;
    margin-right: 10px;
    &:last-child {
      margin: 0;
    }
  }
  &__back,
  li {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: var(--itemtext);
    border-radius: 5px;
    &:hover {
      background: var(--gray);
    }
    &.add {
      color: var(--poblick);
      background: var(--borderInput);
    }
  }
  &__back {
    height: 40px;
    padding: 0 10px;
    p {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    span {
      font-size: 1.2em;
    }
  }

  @media (max-width: $mw3 + px) {
    & {
      height: 110px;
    }
  }
  @media (max-width: $mw5 + px) {
    &__wrap {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    li {
      width: 25px;
      margin-right: 5px;
    }
    &__back,
    li {
      background: var(--gray);
      height: 25px;
    }

    &__back {
      margin-right: 0;
      padding: 0 10px;
      span {
        font-size: 1em;
      }
    }
  }
}
