.shop {
  &__menu {
    position: fixed;
    right: -110%;
    width: 50%;
    height: 100%;
    background: var(--poblick);
    z-index: 3;
    transition: right 0.5s ease 0s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__menu.show {
    right: 0%;
  }
  &__body {
    height: 100%;
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;
  }
  &__burger {
    position: absolute;
    transform: translateX(-110%);
    cursor: pointer;
    top: 0px;
    overflow: hidden;
    width: 30px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 22px;
      height: 22px;
      fill: var(--searchBlock);
    }
  }
  &__btn {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--itemtext);
    color: var(--poblick);
  }
  @media (max-width: 670px) {
    &__menu {
      width: 80%;
    }
    &__body {
      margin-bottom: 50px;
    }
    &__btn {
      bottom: 0;
      position: fixed;
      width: 80%;
    }
  }
  @media (max-width: 400px) {
    &__menu {
      width: 100%;
    }
    &__btn {
      width: 100%;
    }
  }
}
