@import 'src/style/variable.scss';

.wemersion {
  position: relative;
  width: 100%;
  user-select: none;
  cursor: pointer;
  color: var(--itemtext);
  &.sort {
    display: none;
  }
  &__head {
    height: 100%;
    width: 100%;
    height: 45px;
    align-items: center;
    display: grid;
    grid-template: 1fr/ 1fr 20px;
    position: relative;
    & p {
      pointer-events: none;
      letter-spacing: 1px;
    }
  }

  &__blockharmonic {
    z-index: 1;
    transition: max-height 0.5s ease-in-out 0s;
    padding: 0;
    max-height: 0;
    height: auto;
    overflow: hidden;
    width: 100%;
    background: transparent;
  }
  &__blockemersion {
    display: flex;
    flex-direction: column;
    max-height: 600px;
  }

  &.add &__blockharmonic {
    max-height: 600px;
  }

  &__blockemersion > li {
    position: relative;
    color: var(--itemtext);
    margin-bottom: 0px;
    height: 40px;
    cursor: pointer;
    display: grid;
    grid-template: 1fr / auto auto 1fr;

    align-items: center;
    width: 100%;

    p {
      line-height: 130% !important;
    }

    &:last-child {
      margin-bottom: 10px;
    }
  }

  &__text {
    min-width: 80px;
    padding-left: 10px;
  }

  &__ok {
    width: 15px;
    height: 15px;
    border: 1px solid var(--searchBlock);
    position: relative;
    &:after {
      position: absolute;
      content: '';
      top: 6.5px;
      left: 1px;
      width: 5px;
      height: 1.5px;
      background: var(--searchBlock);
      transform: rotate(40deg);
      border-radius: 10px;
      visibility: hidden;
      opacity: 0;
    }
    &:before {
      position: absolute;
      content: '';
      top: 5.5px;
      right: 1px;
      width: 8.5px;
      height: 1.5px;
      background: var(--searchBlock);
      transform: rotate(-45deg);
      border-radius: 10px;
      visibility: hidden;
      opacity: 0;
    }
  }

  &__color {
    justify-self: center;
    width: 15px;
    height: 15px;
    border: 1px solid var(--searchBlock);
    position: relative;
  }

  li.add &__ok {
    &:after {
      visibility: visible;
      opacity: 1;
    }
    &:before {
      visibility: visible;
      opacity: 1;
    }
  }

  &__arrow {
    transition: transform 0.2s ease 0s;
    transform: rotate(0deg);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    justify-self: flex-end;
  }
  &.add &__arrow {
    transform: rotate(90deg);
  }

  & {
    position: relative;
  }
  &.sort {
    display: block;
  }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: -1px;
    width: 100%;
    height: 1px;
    background-color: var(--sortBlock);
  }
  &__head {
    &:before,
    &:after {
      display: none;
    }
  }

  @media (max-width: $mw2 + px) {
    &__head,
    &__blockemersion {
      padding: 0 20px;
    }
  }
}
