@import 'src/style/variable.scss';

.tuner {
  width: 100%;
  height: 130px;

  &__container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__nameCategory {
    color: var(--oldRed);
    user-select: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 30%;
  }

  &__loupe {
    display: block;
    padding: 0 10px;
  }
  &__loupe > div {
    line-height: 32px;
  }

  &__loupe {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  @media (max-width: $mw2 + px) {
    & {
      height: 110px;
    }
  }
  @media (max-width: $mw3 + px) {
    &__container {
      width: 100%;
    }
  }
  @media (max-width: $mw4 + px) {
    &__nameCategory {
      display: none;
    }
  }

  @media (max-width: $mw4 + px) {
    & {
      height: 80px;
    }
  }
}

.sort {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  align-items: center;
  &__block {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding-top: 20px;
  }
  &__block:first-child {
    padding: 0;
  }
  &__title h3 {
    color: var(--searchBlock);
    pointer-events: none;
  }
  &__title {
    height: 40px;
    display: flex;
    align-items: center;
  }
  &__block:last-child {
    padding-right: 0px;
  }
  &__input {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    input {
      color: var(--itemtext);
      background: var(--poblick);
      border: 1px solid var(--itemBorder);
      height: 40px;
      border-radius: 5px;
      padding-left: 16px;
    }
  }
  &__aside {
    padding-right: 40px;
    height: 100%;
  }

  &__blockone {
    display: flex;
    flex-direction: column;
  }

  &__filter {
    position: relative;
    height: 30px;
    width: 30px;
    user-select: none;
    cursor: pointer;
    padding: 5px;
    display: none;
    svg {
      fill: var(--searchBlock);
      width: 100%;
      height: 100%;
    }
  }

  &__add {
    width: 100%;
    padding-top: 20px;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    height: 35px;
    padding: 0 10px;
    //  margin-top: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: var(--borderforschedul);
    color: var(--searchBlock);
    margin-right: 10px;
  }
  &__close {
    margin-left: 10px;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0.8;
    background: var(--borderInput);
    border-radius: 100%;
    svg {
      width: 12px;
      height: 100%;
      fill: var(--borderforschedul);
    }
  }
  @media (max-width: $mw1 + px) {
    &__aside {
      padding-right: 20px;
      height: 100%;
    }
  }
  @media (max-width: $mw2 + px) {
    &__block:last-child {
      padding-bottom: 20px;
    }
    &__aside {
      // display: none;
    }
    &__add {
      padding-top: 0;
      padding-bottom: 40px;
    }
    &__filter {
      display: flex;
      margin-left: 20px;
    }
    &__title {
      padding-left: 20px;
    }

    &__blockone {
      width: 100%;
      flex-direction: column;
      height: 100%;
      justify-content: flex-start;
    }

    &__blockone {
      overflow-x: auto;
    }
  }
  @media (max-width: $mw4 + px) {
    & {
      justify-content: space-between;
    }
    & {
      justify-content: space-between;
    }
  }
  @media (max-width: 670px) {
  }
  @media (max-width: $mw5 + px) {
    &__add {
      padding-top: 0;
      padding-bottom: 30px;
    }
  }
}

.asidturn {
}

.likes {
  .sort {
    &__block:last-child {
      padding-right: 0px;
    }
    @media (max-width: $mw3 + px) {
      & {
        justify-content: flex-end !important;
      }
      &__blockone {
        justify-content: flex-end !important;
        width: 100%;
      }
      &__block {
        justify-content: flex-end !important;
        padding-right: 20px;
      }
    }
  }
}

.saleasideblock {
  width: 100%;
  user-select: none;
  cursor: pointer;
  color: var(--itemtext);
  ul {
    display: flex;
  }
  li {
    position: relative;
    color: var(--itemtext);
    margin-bottom: 0px;
    height: 40px;
    cursor: pointer;
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0 !important;

    p {
      line-height: 130% !important;
    }

    &:last-child {
      margin-bottom: 10px;
    }
  }
  @media (max-width: $mw2 + px) {
    ul {
      padding-left: 20px;
    }
  }
}
