@import 'src/style/variable.scss';

.searchblock {
  height: 100%;
  display: flex;
  align-items: center;
  margin-right: 40px;
  position: relative;
  width: 100%;
  &__input {
    max-width: 300px;
    width: 100%;
    transition: width 0.2s linear 0s;
    overflow: hidden;
    position: relative;
  }

  &__items {
    position: absolute;
    width: 100%;
    top: 110%;
    max-height: 0;
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    background: var(--gray);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    transition: all 0.2s linear 0s;
    overflow: hidden;
    border-radius: 2px;
    &.add {
      opacity: 1;
      visibility: visible;
      max-height: 120px;
    }
  }

  &__item {
    padding: 0 10px;
    display: flex;
    align-items: center;
    height: 40px;
    color: var(--searchBlock);
    background: var(--gray);
    cursor: pointer;
    outline: none;
    border: none;
    width: 100%;
    &:hover {
      background: var(--oldRed);
      color: #fff;
    }
    p {
      line-height: 130%;
    }
  }

  &__item:focus {
    background: var(--oldRed) !important;
    color: #fff;
  }

  &__text {
    position: absolute;
    top: auto;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    color: var(--itemBorder);
    transition: left 0.5s ease 0s;
    pointer-events: none;
    user-select: none;
  }
  &__text.all.focus {
    left: 25px;
  }
  &__text.old {
    P {
      display: block;
    }
  }

  &__text {
    P {
      display: none;
    }
  }

  input {
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    font-weight: 500;
    font-size: 16px;

    padding-left: 10px;
    border: 1px solid var(--itemBorder);
    color: var(--itemtext);
    background: var(--poblick);
    border-radius: 2px;
  }

  &__loupe {
    cursor: pointer;
    height: 40px;
    width: 40px;
    padding: 10px;
    svg {
      width: 100%;
      height: 100%;
      fill: var(--itemtext);
    }
  }
  @media (max-width: $mw5 + px) {
    & {
      margin-right: 0;
    }
    &__input {
      width: 0px;
    }
    &__input.add {
      width: 70%;
      input {
        padding-left: 10px;
      }
    }
    &__loupe {
      cursor: pointer;
      height: 30px;
      width: 30px;
      padding: 5px;
    }
    input {
      box-sizing: border-box;
      width: 100%;
      height: 35px;
    }
  }
}
