@import 'src/style/variable.scss';

.sortblock {
  width: 32px;
  height: 100%;
  margin-right: 15px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  display: none;
  &__body {
    width: 100%;
    height: 22px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  svg {
    width: 100%;
    height: 100%;
    fill: var(--searchBlock);
  }
  &__arrow {
    width: 12px;
    height: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    align-self: flex-end;
    transform: rotate(90deg);
  }

  &__block {
    width: 22px;
    height: 22px;
    cursor: pointer;
  }
  &.sort .sortblock__block {
    transform: rotate(180deg) scaleX(-1);
  }

  @media (max-width: 670px) {
    & {
      display: none;
    }
  }
}

.sortprice {
  display: flex !important;
  height: 100% !important;
  flex-direction: column;
  width: 80%;
  &__price {
    display: flex;
    height: 100%;
    align-items: flex-start;
    width: 100%;
  }
  &__input {
    width: 55px;
    height: 30px;

    input {
      border-radius: 5px;
      width: 100%;
      height: 100%;
      border: 1px solid var(--itemBorder);
      color: var(--itemtext);
      background: var(--poblick);
      padding: 5px;
    }
  }
  &__input:last-child {
    margin-left: 20px;
  }

  &__line {
    margin: 20px 0 10px 0;
    width: 100%;
  }

  &__lineblock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    max-width: 220px;
    width: 100%;
  }

  &__slider {
    border-radius: 5px;
    background: #e0e0e0;
    background: linear-gradient(left top, #e0e0e0, #eeeeee);
    width: 100%;
    height: 10px;
  }

  &__thumb {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    position: absolute;
    background: var(--thumbcircle);
    cursor: pointer;
    display: block;
    touch-action: none;
  }
  &__thumblow {
    left: 0px;
  }
  &__thumbtall {
    left: calc(100% - 20px);
  }
}
